<template>
    <div class="city">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left i1"></i>
            </a>
            <span>{{city ? city.name : ""}}</span>
            <router-link to="/home">
                <span>切换城市</span>
            </router-link>
        </header>
        <section class="s1">
            <div class="iDiv"><input type="search" placeholder="输入学校、商务楼、地址" clearable class="input1" v-model="input1"></input></div>
            <div class="iDiv"><el-button type="primary" class="btn1" @click="Submission">提交</el-button></div>
        </section>
        <section class="s2">
            <p class="s2p" v-if="isShow1">搜索历史</p>
            <ul class="s2u1">
                <li v-for="c in search"  @click="positions(c)">
                    <h4>{{c ? c.name : ""}}</h4>
                    <p>{{c ? c.address : ""}}</p>
                </li>
                <li class="btn2" v-if="isShow2" @click="clear">清空所有</li>
            </ul>
        </section>
    </div>
</template>

<script>

    export default {
        name: "City",
        data(){
            return{
                city_id:"",
                city:[],
                input1:"",
                search:[],
                // a:[],
                //控制搜索历史的显隐
                isShow1:true,
                //控制清除历史记录按钮的显隐
                isShow2:false,
            }
        },
        created(){
            this.city_id = this.$route.params.cityId;
            localStorage.setItem("city_id",JSON.stringify(this.city_id))
            this.axios.get("/v1/cities/" + this.city_id).then(res=>{
                this.city = res;
            }).catch(err=>{});
            if(localStorage.getItem("position")){
                this.search = JSON.parse(localStorage.getItem("position"));
                this.isShow2 = true;
            }
        },
        methods:{
            Submission(){
                if (this.input1 != ""){
                    this.axios.get("/v1/pois",{
                        params:{
                            city_id:this.city_id,
                            keyword:this.input1,
                            type:"search"
                        }
                    }).then(res=>{
                        if(res == "") {
                            this.search = [{name:"很抱歉！无搜索结果"}];
                        }else {
                            this.search = res;
                            // console.log(this.search);
                        }
                    }).catch(err=>{});
                    this.isShow1 = false;
                }
            },
            positions(b){
                let a;
                if(localStorage.getItem('position')==null){
                    a = [];
                    a.push(b)
                    localStorage.setItem("position",JSON.stringify(a));
                }else{
                    a = JSON.parse(localStorage.getItem('position'));

                    localStorage.setItem("position",JSON.stringify(a));
                }
                // this.$router.push({name:'m',params:{geohash:b.geohash,latitude:b.latitude,longitude:b.longitude}});
                this.$router.push("/msite");
                localStorage.setItem("msite",JSON.stringify(b));
            },
            clear(){
                localStorage.removeItem("position");
                this.search = [];
                this.isShow2 = false;
            }
        }
    }

</script>

<style scoped>
    .city{
        overflow: hidden;
    }

    .i1{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        color: #fff;
        line-height: 2.2rem;
        margin-left: .4rem;
    }
    header>span{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    header>a>span{
        right: .4rem;
        font-size: .6rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .s1{
        margin-top: 2.4rem;
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-top: .4rem;
        overflow: hidden;
    }
    .iDiv{
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    .input1{
        width: 100%;
        border: 1px solid #e4e4e4;
        font-size: .65rem;
        padding: .3rem;
        color: #333;
        outline: none;
        border-radius: .1rem;
        margin-bottom: .4rem;
        height: 1.4rem;
    }
    .btn1{
        width: 100%;
        border-radius: .1rem;
        margin-bottom: .4rem;
        height: 1.4rem;
        line-height: 0rem;
        background-color: #3190e8;
        font-size: .65rem;
        color: #fff;
    }
    .s2{
        width: 100%;
        overflow: hidden;
    }
    .s2p{
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-left: .5rem;
        font: .475rem/.8rem Microsoft YaHei;
    }
    .s2u1{
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
    }
    .s2u1>li{
        margin: 0 auto;
        padding-top: .65rem;
        border-bottom: 1px solid #e4e4e4;
    }
    .s2u1>li>h4{
        margin: 0 auto .35rem;
        width: 90%;
        font-size: .65rem;
        color: #333;
    }
    .s2u1>li>p{
        width: 90%;
        margin: 0 auto .55rem;
        font-size: .45rem;
        color: #999;
    }
    .btn2{
        font-size: .7rem;
        color: #666;
        text-align: center;
        line-height: 2rem;
        background-color: #fff;
    }
</style>